* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Times New Roman, Times, serif;
}

header {
  position: relative;
}

header nav {
  width: 100%;
  z-index: 10;
  background-color: #fff;
  border-bottom: 1px solid gray;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
}

header nav .logo h2 {
  color: red;
  margin-left: 1em;
  font-size: 2em;
  font-weight: bolder;
  animation-duration: 3s;
  animation-iteration-count: infinite;
}

header nav .logo h2 span {
  color: #000;
  font-size: 1.2em;
}

header nav .navigation {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

header nav .navigation .menu {
  z-index: 1;
  justify-content: center;
  align-items: center;
  transition: all .5s;
  display: flex;
}

header nav .navigation .menu li {
  list-style-type: none;
}

header nav .navigation .menu li a {
  color: #000;
  text-transform: uppercase;
  padding: 40px 25px;
  font-size: 20px;
  font-weight: bold;
  line-height: 1;
  text-decoration: none;
  transition: all .3s;
  display: block;
}

header nav .navigation .menu li a:hover {
  box-shadow: inset 0 -5px #b29f9f, inset 700px 0 #24222208;
}

header nav .navigation .hambg {
  width: 30px;
  height: 4px;
  cursor: pointer;
  z-index: 10;
  background: #000;
  border-radius: 10px;
  transition: all .3s;
  position: relative;
}

header nav .navigation .hambg:before, header nav .navigation .hambg:after {
  content: "";
  height: 4px;
  background: #000;
  border-radius: 10px;
  transition: all .3s;
  position: absolute;
  right: 0;
}

header nav .navigation .hambg:before {
  width: 20px;
  top: -10px;
}

header nav .navigation .hambg:after {
  width: 25px;
  top: 10px;
}

header nav .navigation .toggle-menu {
  width: 30px;
  height: 100%;
  z-index: 3;
  cursor: pointer;
  opacity: 0;
  position: absolute;
}

header nav .navigation .hambg, header nav .navigation .toggle-menu {
  display: none;
}

header nav .navigation input:checked ~ .hambg {
  background: none;
}

header nav .navigation input:checked ~ .hambg:before {
  width: 30px;
  top: 0;
  transform: rotate(-45deg);
}

header nav .navigation input:checked ~ .hambg:after {
  width: 30px;
  top: 0;
  transform: rotate(45deg);
}

header nav .navigation input:checked ~ .menu {
  right: 0;
}

